.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.logo-wrapper {
	padding: 0.5rem;
	box-sizing: border-box;
	/* background: linear-gradient(#eeeeee, #fff); */
	background: linear-gradient(to right, #eee, #eee, #eee, #eee, #eee, #fff, #fff, #fff, #fff, #fff);
}

.MuiInput-root > .MuiSelect-select {
	display: flex;
	align-items: center;
}

div.backup .MuiSelect-select {
	font-size: 14px;
	word-break: break-word;
	text-overflow: inherit;
	overflow: auto;
	width: 241px;
	white-space: inherit;
}

.MuiAutocomplete-listbox {
	/* background-color: #121212; */
}

.MuiPaper-root {
	backdrop-filter: blur(10px);
}
.MuiBackdrop-root {
	backdrop-filter: blur(10px);
}
